@import url('https://fonts.googleapis.com/css2?family=Didact+Gothic&display=swap');

.modal-layer{
    width: 20vw;
    height: 18vh;
    padding: 1%;
    border: 1px solid gray;
    border-radius: 5px;
    font-size: 80%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-content: space-between;
}

.modal-title{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.modal-head{
    display: flex;
    flex-direction: column;
}

.modal-row{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.modal-data{
    width: 33%;
    border-bottom: solid 1px;
    border-color: rgb(230, 230, 230);
}

.modal-grey{
    color: grey;
}

.modal-blue{
    color: blue;
}

.modal-red{
    color: red;
}

.modal-left{
    display: flex;
    justify-content: flex-end;
}

.modal-font{
    font-family: sans-serif;
    font-size: 12px;
}